html {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
}

body {
  box-sizing: border-box;
  margin: 0;
  --header-high: 60px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  height: var(--header-high);
}

.logolink {
  position: relative;
  width: 40px;
  height: 40px;
}

.logo {
  width: 40px;
  height: 40px;
}

/* #region nav */

ul {
  list-style-type: none;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
}

.nav__list > li:last-child > a {
  padding-right: 0;
}

.nav__item:last-child {
  margin-right: 0;
  padding-right: 0;
}

.nav__link:link {
  display: inline-block;
  font-weight: 500;
  font-size: 12px;
  line-height: var(--header-high);
  text-transform: uppercase;
  height: var(--header-high);
  padding: 0 10px;
  color: black;
  text-decoration: none;
}

.nav__link:visited {
  color: black;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link:active {
  color: #00acdc;
}

.nav__link:active::after {
  content: "";
  height: 2px;
  display: block;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 8px;
  position: relative;
  bottom: 4px;
}

.is-active:link {
  color: #00acdc;
}

.is-active:visited {
  color: #00acdc;
}

.is-active::after {
  content: "";
  height: 2px;
  display: block;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 8px;
  position: relative;
  bottom: 4px;
}

/* #endregion */
